<template>
  <!-- 头部导航栏 -->
  <div class="head">
    <div class="left_button" @click="toggleClick">
      <el-icon>
        <Expand />
      </el-icon>
    </div>
    <div class="navigation">
      <el-breadcrumb separator="/">
        <template v-for="(item, index) in breadList">
          <el-breadcrumb-item v-if="item.name" :key="index" :to="item.path">{{ item.name }}</el-breadcrumb-item>
        </template>
      </el-breadcrumb>
    </div>
    <div class="r_icon">
      <el-dropdown trigger="click">
        <span class="cli">
          <el-avatar :size="46" :src="image_url" />
          <h1 class="na"> {{ name }}</h1>
        </span>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item @click="onSubmit">个人页面</el-dropdown-item>
            <el-dropdown-item @click="logout">登出</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { store } from "@/utils/global.js";
import { routes } from "@/router/index.js";

export default {
  data() {
    return {
      store,
      name: sessionStorage.getItem("name"),
      image_url: sessionStorage.getItem("image_url"),
      circleUrl: "http://101.200.87.232:19000/user/645b93886ae64891d1b738d0.jpg",
      breadList: [{
        "fullPath": "/manager/home",
        "path": "/manager/home",
        "name": "管理员首页",
        "meta": {},
        "href": "/manager/home"
      }],
    }
  },

  created() {
    this.getMsg()
  },
  mounted() {
  },
  methods: {
    getMsg() {
      setTimeout(() => {
        this.name = sessionStorage.getItem("name");
        this.image_url = sessionStorage.getItem("image_url");
      }, 100);
    },
    toggleClick() {
      store.manager_isCollapse = !store.manager_isCollapse
    },
    async logout() {
      this.$router.push('/manager/login')
      sessionStorage.clear();
    },
    onSubmit() {
      this.$router.push('/manager/personalPage').catch(() => { })
    }
  },
  watch: {
    $route() {
      this.breadList = this.$route.matched;
      this.name = sessionStorage.getItem("name");
      this.image_url = sessionStorage.getItem("image_url");
    }

  }
};
</script>

